.App {
  text-align: center;
  background: url("../public/bg2.jpg");
  background-size: cover;
  background-position: center;
}

.mom-photo {
  height: 100%;
  pointer-events: none;
}

.photo-container {
  border-radius: 50%;
  overflow: hidden;
  height: 60vmin;
}

.title {
  color: #f1acd1;
  font-size: 26px;
  font-weight: bold;
}

.gift-text {
  color: #f09abf;
  padding: 20px;
}

.gift-img {
  height: 40vmin;
}

.movie-img {
  height: 60vmin;
}

